
:root{
	/* --title-font: Georgia, 'Times New Roman', Times, serif; */
	--title-font: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	--title-font-size: 23px;
	--title-font-weight: 600;
}

html {
	/* background-color: rgba(78, 93, 215, 0.75); */
	/* background-image: linear-gradient(rgba(36, 167, 248, 0.9), rgba(36, 167, 248, 0.9),  rgba(0, 0, 139, 0.85)); */
	background-image: linear-gradient(rgba(78, 93, 215, 0.75), rgba(78, 93, 215, 0.9),  rgba(0, 0, 139, 0.93));

	/* background-color: rgba(0, 68, 255, 0.795); */
	/* background-color: rgba(0, 0, 139, 0.85); */
	height: 101vh;
}

html * {
	color: rgba(255, 255, 255, 0.8);
	/* color: rgba(78, 93, 215, 0.9); */
}

body {
	z-index: -10;
	margin:0;
}

body > div {
	display: flex;
	margin: 0 auto;
	justify-content: center;
}

#main > form {
	border-bottom: 1px solid rgba(252, 250, 250, 0.8);
}

#main > * {
	color: rgba(255,255,255,0.7)
}

.filter{
	position: absolute;
	height: 40%;
	width: 100%;
	z-index: 100;
	pointer-events: none;
	border-radius:4px;
}

.filter-top{
	top: 0;
	background-image: linear-gradient(rgba(83, 96, 207, 1), rgba(60, 70, 186, 0));
}
.filter-bottom{
	bottom: 0;
	background-image: linear-gradient(rgba(48, 56, 174, 0), rgba(21, 26, 150, 1));
}

#main{
	padding-bottom: 5px;
	padding-top: 5px;
	position: absolute;
	left: 0;
	right: 0;
	top: 300px;
	z-index: 100;
	background: rgba(0,0,0,0.05);
	transform: rotate(0)
}

.chart-title {
	display: flex;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	font-family: var(--title-font);
	font-size: var(--title-font-size);
	font-weight: var(--title-font-weight);
	padding: 5px;
}

.query-field{
	border: none;
	outline: none;
	cursor: text;
	height: 90%;
	text-align: center;
	font-family: var(--title-font);
	font-size: var(--title-font-size);
	font-weight: var(--title-font-weight);
	background-color: transparent;
}

.query-field::-webkit-input-placeholder{
	color: rgba(255,255,255, 0.25);
}

.query-field::-moz-placeholder{
	color: rgba(255,255,255, 0.25);
}

.query-field:-ms-input-placeholder{
	color: rgba(255,255,255, 0.25);
}

.query-field::-ms-input-placeholder{
	color: rgba(255,255,255, 0.25);
}

.query-field::placeholder{
	color: rgba(255,255,255, 0.25);
}

.query-field:hover{
	background-color: rgba(255,255,255, 0.1);
	border-radius: 4px;
}


