	font-weight: 600;
	font-size: 17px;
	text-decoration: underline;
	color: transparent;
	/* color: rgba(0, 0, 139, 0.5); */
}
.read-more{
	color: transparent
}

.info {
	line-height: 175%;
	padding-bottom: 8px;
	margin-top: -4px;
	color: transparent;
	/* color: rgba(255,255,255, 0.5); */
