}


.article-title{
	font-size: 24px;
}

.debug {
	border: 1px solid red;
	background: rgba(255,0,0,0.5);
}

.articles-container{
	display: flex;
	flex-direction: column;
	width: 67%;
}

.article-info-container{
	height: 100%;
	justify-content: space-between;
	display: flex;
	flex-direction: column;
	/* box-shadow:  0 -2px 3px -2px black inset; */
	box-shadow:  0px 0px 0px 0px black inset;
}

.article{
	height: 90%;
	overflow-y: scroll;
	overflow-x: hidden;
	/* -webkit-transition: fill 0.15s, opacity 0.15s, background-color 1s;
	-moz-transition:    fill 0.15s, opacity 0.15s, background-color 1s;
	-ms-transition:     fill 0.15s, opacity 0.15s, background-color 1s;
	-o-transition:      fill 0.15s, opacity 0.15s, background-color 1s;
	transition:         fill 0.15s, opacity 0.15s, background-color 1s; */
	border-radius: 4px;
	padding: 8.5px;
	padding-bottom: 15px;
}



.article::-webkit-scrollbar {
	width: 6px;
	border-radius: 6px;
}
.article::-webkit-scrollbar-corner {
	background: rgba(0,0,0,0);
	height: 1px;

}
.article::-webkit-scrollbar-track-piece{
    border-radius: 6px;
	background: transparent;
}
.article::-webkit-scrollbar-thumb{
	background: rgba(255, 255, 255, 0.7);
	border-radius: 6px;
}

.dot{
	cursor: pointer;
}

.dot {
  transition:         fill 0.15s, opacity 0.15s, background-color 1s;
}

footer{
	/* margin-top: 5px; */
	margin-right: 5px;
	margin-bottom: 13px;
	text-align: right;
	height: 3px;
	font-size: 7px;
}

