.articles-list-container{
	height: 44%;
	margin-top: 5px;
	position: relative;
}

#articles-list {
	margin-top: 0px;
	height: 96%;
	overflow-y: scroll;
	overflow-x: hidden;
	padding-top: 0;
	padding-bottom: 10px;
	/* box-shadow: black 1px 1px 5px 0px; */
	/* box-shadow: transparent; */
	border-radius: 4px;
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	line-height: 150%;
	perspective: 500px;
}

.article-list-item{
	width: 97%;
	/* border: 1px solid red; */
	padding: 8px;
	margin: 8px auto;
	/* background: rgba(255,255,255, 0.1); */
	border-radius: 4px;
	cursor: pointer;
	color: rgba(255,255,255, 0.7);
	font-size: 17px;
	line-height: 175%;
}



ul {
  list-style-type: none;
  padding: 0;
}

